<template>
    <div class="g-footer">
        <div class="m-qrcode">
            <ul class="qrcodes">
                <li class="qrcode" v-for="(qrcode, index) in qrcodes" :key="index">
                    <div class="qrcode-desc">
                        <i :class="qrcode.icon">{{qrcode.desc}}</i>
                    </div>
                    <div class="qrcode-show">
                        <img :src="qrcode.qrcode">
                        <p>{{qrcode.desc}}</p>
                    </div>
                </li>
            </ul>
        </div>
        <p class="m-copyright">©2018 北京中电高科技电视发展有限公司    京ICP备13051846号</p>
    </div>
</template>

<script>
export default {
    name: 'pfooter',
    data() {
        return {
            qrcodes: [
                {
                    desc: 'Andriod版',
                    icon: 'andriod',
                    qrcode: 'http://123.206.33.94/Public/Home/weishi/statics/images/weChat2.png',
                },
                {
                    desc: 'IOS版',
                    icon: 'ios',
                    qrcode: 'http://123.206.33.94/Public/Home/weishi/statics/images/weChat2.png',
                },
                {
                    desc: '扫码有惊喜',
                    icon: 'code',
                    qrcode: 'http://123.206.33.94/Public/Home/weishi/statics/images/weChat2.png',
                },
            ],
        };
    },
};
</script>


<style lang="less">
.g-footer {
    padding-top: 80px;
    background-color: #40393B;
    .m-qrcode {
        width: 930px;
        margin: 0 auto 65px;
        border-radius: 10px;
        background-color: #474042;
    }
    .qrcodes {
        display: flex;
    }
    .qrcode {
        position: relative;
        flex: 1;
        height: 160px;
        color: #fff;
        cursor: pointer;
        &:first-child {
            .qrcode-desc {
                border-left-width: 0;
            }
        }
        &:hover {
            .qrcode-show {
                display: block;
            }
        }
    }
    .qrcode-desc {
        margin: 22px 0;
        line-height: 116px;
        text-align: center;
        border-left: 1px solid #40393B;
        i {
            font-style: normal;
            &::before {
                content: '';
                display: inline-block;
                width: 40px;
                height: 44px;
                margin-right: 22px;
                background: url("../assets/images/ios.png") no-repeat;
                background-size: contain;
                vertical-align: middle;
            }
        }
        .ios::before {

        }
    }
    .qrcode-show {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center;
        background-color: #514D4C;
        img {
            margin-top: 38px;
            width: 60px;
            height: 60px;
        }
        p {
            margin-top: 19px;
        }
    }
}
.m-copyright {
    padding: 7px 0;
    background-color: #393335;
    text-align: center;
    font-size: 12px;
    color: #867D7F;
    line-height: 36px;
}
</style>



