<template>
    <div class="m-vnews">
        <c-tit tit="新闻动态"></c-tit>
        <div class="vnews g-container">
            <div class="lay-flex">
                <a class="video" :href="video.url">
                    <div class="video-cover">
                        <img :src="video.cover">
                    </div>
                    <p class="video-title">{{video.title}}</p>
                </a>
                <div class="news">
                    <ul class="new-list">
                        <li class="new" v-for="(article, index) in articles" :key="index">
                            <a :href="article.url" class="new-inner">
                                <h4 class="new-title">{{article.title}}</h4>
                                <div class="new-info">
                                    <div class="new-cate">{{article.cate}}</div>
                                    <div class="new-date">{{article.date}}</div>
                                </div>
                                <div class="new-line"></div>
                            </a>
                        </li>
                    </ul>
                    <div class="clearfix"></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import CTit from './common/tit';

export default {
    name: 'livestream',
    components: {
        CTit,
    },
    data() {
        return {
            video: {
                url: '/',
                cover: 'http://bigvideo.oss-cn-hangzhou.aliyuncs.com/pic/1512195269528.jpg?size=1366x768',
                title: 'CCTV微视参加央视2018年世界杯 新媒体广告产品发布会',
            },
            articles: [
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
                {
                    url: '#',
                    title: '斯诺克世锦赛再燃战火 来CCTV微视看直播赢球杆',
                    cate: '近期新闻',
                    date: '2018-05-08',
                },
            ],
        };
    },
};
</script>

<style lang="less">
.m-vnews {
    padding-top: 30px;
    background-color: #f6f6f6;
    .vnews {
        margin-top: 40px;
        padding-bottom: 48px;
    }
    .video {
        width: 31.48%;
        text-decoration: none;
    }
    .video-cover {
        height: 210px;
        overflow: hidden;
        img {
            width: 100%;
            min-height: 210px;
        }
    }
    .video-title {
        margin-top: 18px;
        color: #000;
        font-size: 16px;
        line-height: 25px;
    }
    .news {
        flex: 1;
    }
    .new {
        float: left;
        box-sizing: border-box;
        width: 50%;
        padding-left: 40px;
        padding-bottom: 10px;
    }
    .new-inner {
        display: block;
        position: relative;
        height: 78px;
        text-decoration: none;
        &:hover {
            .new-title,
            .new-cate,
            .new-date {
                color: #FF6A66;
            }
            .new-line::after {
                width: 100%;
            }
        }
    }
    .new-title {
        color: #000;
        font-size: 16px;
        line-height: 25px;
    }
    .new-info {
        position: absolute;
        display: flex;
        bottom: 5px;
        left: 0;
        width: 100%;
        justify-content: space-between;
        font-size: 12px;
        line-height: 14px;
    }
    .new-cate {
        color: #000;
    }
    .new-date {
        color: #505050;
    }
    .new-line {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #eee;
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 0;
            background-color: #FF6A66;
            transition: width 0.6s ease-in-out;
        }
    }
}
</style>



