<template>
    <div class="m-livestream">
        <c-tit tit="正在直播"></c-tit>
        <div class="livestaream g-container">
            <div class="leftnav">
                <div class="prev"><i class="e-icon_up"></i></div>
                <ul class="channels">
                    <li v-for="(channel, index) in channels"
                        :key="index"
                        :class="{'item': true, 'active': index === 0,}">
                        <a :href="channel.url" target="_blank">{{channel.name}}</a>
                    </li>
                </ul>
                <div class="next"><i class="e-icon_down"></i></div>
            </div>
            <div class="rightnav">
                <ul class="videos">
                    <li class="video" v-for="(video, index) in videos" :key="index">
                        <a :href="video.url" target="_blank">
                            <div class="video-cover">
                                <img :src="video.cover">
                                <div class="video-persons" v-if="video.persons">
                                    <i class="e-icon_person"></i>{{video.persons}}
                                </div>
                                <div class="video-mask">
                                    <div class="video-mask_inner">
                                        <i class="e-icon_video"></i>
                                    </div>
                                </div>
                            </div>
                            <p class="video-desc">{{video.desc}}</p>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import CTit from './common/tit';

export default {
    name: 'livestream',
    components: {
        CTit,
    },
    props: {
        channels: {
            type: Array,
            default: () => [],
        },
        videos: {
            type: Array,
            default: () => [],
        },
    },
};
</script>

<style lang="less">
.m-livestream {
    padding-top: 50px;
    padding-bottom: 22px;
    .livestaream {
        margin-top: 30px;
        display: flex;
    }
    .leftnav {
        width: 135px;
        height: 262px;
        position: relative;
        padding: 33px 0;
        background-color: #292929;
        .prev,
        .next {
            position: absolute;
            left: 3px;
            right: 3px;
            height: 20px;
            background-color: #3F3F43;
            cursor: pointer;
            text-align: center;
            vertical-align: middle;
        }
        .prev {
            top: 5px;
        }
        .next {
            bottom: 5px;
        }
    }
    .rightnav {
        margin-left: 16px;
        flex: 1;
    }
    .channels {
        height: 100%;
        overflow: auto;
        .item {
            margin-top: 1px;
            background-color: #333337;
            line-height: 32px;
            text-align: center;
            font-size: 12px;
            &:first-child {
                margin-top: 0;
            }
            &:hover {
                background-color: #FF3C3D;
                a {
                    color: #FFFEFE;
                }
            }
        }
        a {
            display: block;
            text-decoration: none;
            color: #8c8c8c;
        }
    }
    .videos {
        a {
            text-decoration: none;
        }
    }
    .video {
        float: left;
        box-sizing: border-box;
        width: 25%;
        padding-left: 14px;
        &:hover {
            .video-mask {
                display: block;
            }
            .video-desc {
                color: #FF3C3D;
            }
        }
    }
    .video-cover {
        position: relative;
        width: 100%;
        img {
            width: 100%;
            height: 127px;
            vertical-align: top;
        }
    }
    .video-desc {
        margin-top: 12px;
        padding-bottom: 12px;
        font-size: 13px;
        line-height: 1;
        color: #333;
    }
    .video-persons {
        position: absolute;
        top: 6px;
        right: 10px;
        line-height: 16px;
        color: #F5F5F5;
    }
    .video-mask {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #FF3C3D;
        opacity: .8;
        cursor: pointer;
        .e-icon_video {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 42px;
            height: 42px;
            transform: translate(-50%, -50%);
        }
    }
    .video-mask_inner {
        position: absolute;
        top: 5px;
        left: 5px;
        right: 5px;
        bottom: 5px;
        border: 1px solid #FFC0C0;
    }
}
</style>



