<template>
    <div class="m-banner">
        <div class="banner">
            <img class="banner-img" :src="bannerImg">
            <div class="phone">
                <video class="video1" autoplay loop>
                    <source src="../assets/test.mp4" type="video/mp4">
                    您的浏览器不支持 video 标签。
                </video>
                <video class="video2" autoplay loop>
                    <source src="../assets/test.mp4" type="video/mp4">
                    您的浏览器不支持 video 标签。
                </video>
            </div>
            <div class="download">
                <div class="btn">
                    <a :href="iosUrl" class="ios" target="_blank"></a>
                    <a :href="andriodUrl" class="andriod" target="_blank"></a>
                </div>
                <div class="qrcode">
                    <img :src="appCode">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'banner',
    props: {
        bannerImg: String,
        appCode: String,
        andriodUrl: String,
        iosUrl: String,
        videoUrl: String,
    },
};
</script>


<style lang="less">
.m-banner {
    .banner {
        position: relative;
        width: 1280px;
        height: 500px;
        margin: 0 auto;
    }
    .banner-img {
        width: 100%;
        min-height: 100%;
    }
    .phone {
        position: absolute;
        top: 0;
        left: 15%;
        width: 331px;
        height: 100%;
        background: url("../assets/images/phone.png") no-repeat;
        background-size: contain;
    }
    .video1 {
        position: absolute;
        top: 66px;
        left: 67px;
        width: 197px;
        height: 108px;
    }
    .video2 {
        position: absolute;
        bottom: 0;
        left: 88px;
        width: 412px;
        height: 232px;
    }
    .download {
        position: absolute;
        right: 18%;
        top: 50%;
        .btn {
            float: left;
            a {
                display: block;
                width: 170px;
                height: 42px;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
        .andriod {
            margin-top: 20px;
            background-image: url("../assets/images/btn_andriod.png");
        }
        .ios {
            background-image: url("../assets/images/btn_iphone.png");
        }
        .qrcode {
            float: left;
            margin-left: 40px;
            width: 100px;
            height: 100px;
            img {
                width: 100%;
            }
        }
    }
}
</style>



