<template>
    <div class="g-doc">
        <h-menu></h-menu>
        <h-banner
            :banner-img="banner.bannerImg"
            :app-code="banner.appCode"
            :andriod-url="banner.andriodUrl"
            :ios-url="banner.iosUrl"
            :video-url="banner.videoUrl"
            ></h-banner>
        <!-- 正在直播 -->
        <live-stream
            :channels="liverStream.channels"
            :videos="liverStream.videos"
            ></live-stream>
        <!-- 微视频 -->
        <s-videos :videos="sVideos"></s-videos>
        <!-- 热门精华帖 -->
        <hot-news></hot-news>
        <!-- 新闻动态 -->
        <v-news></v-news>
        <!-- 版权 -->
        <p-footer></p-footer>
    </div>
</template>

<script>
import HMenu from './components/hmenus';
import HBanner from './components/banner';
import LiveStream from './components/livestream';
import SVideos from './components/svideos';
import HotNews from './components/hotnews';
import VNews from './components/vnews';
import PFooter from './components/pfooter';

import mockData from './mock.json';

export default {
    name: 'App',
    components: {
        HMenu,
        HBanner,
        LiveStream,
        SVideos,
        HotNews,
        VNews,
        PFooter,
    },
    data() {
        return {
            banner: {
                bannerImg: '',
                appCode: '',
                andriodUrl: '',
                iosUrl: '',
                videoUrl: '',
            },
            liverStream: {
                channels: null,
                videos: null,
            },
            sVideos: null,
        };
    },
    mounted() {
        this.parseData(mockData.data);
    },
    methods: {
        parseData(data) {
            this.banner = {
                bannerImg: data.bannerinfo.imgurl,
                appCode: data.setinfo.img_app,
                andriodUrl: data.setinfo.a_url,
                iosUrl: data.setinfo.ios_url,
                videoUrl: data.setinfo.video_url,
            };
            this.liverStream = this.parseLiveStream(data);
            this.sVideos = this.parseSVideos(data);
        },
        parseLiveStream(data) {
            const channels = [];
            const videos = [];
            (data.channeldata || []).forEach((channel) => {
                channels.push({
                    name: channel.channelname,
                    url: `/Channel/index/id/${channel.id}.html`,
                });
            });
            (data.livedata || []).forEach((video) => {
                videos.push({
                    url: `/Live/index/id/${video.ownerid}.html`,
                    cover: video.img,
                    desc: video.title,
                    // persons: 12332,
                });
            });
            return {
                channels,
                videos,
            };
        },
        parseSVideos(data) {
            const videos = [];
            (data.videodata || []).forEach((video) => {
                videos.push({
                    url: `/Microvideo/index/id/${video.id}.html`,
                    cover: video.imgurl,
                    desc: video.name,
                    count: video.num,
                });
            });
            return videos;
        },
    },
};
</script>

<style lang="less">
@import url('./assets/styles/common.less');
</style>




