<template>
    <div class="m-menus g-container">
        <img src="../assets/images/logo.png" class="logo">
        <div class="navs-wrapper">
            <a href="javascript:;" class="menu-icon"></a>
            <ul class="navs">
                <li v-for="(nav, index) in navs" :key="index">
                    <a :href="nav.url" :class="{'active': nav.url == '/'}">{{nav.name}}</a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name: 'menus',
    data() {
        return {
            navs: [
                {
                    name: '首页',
                    url: '/',
                },
                {
                    name: '微视动态',
                    url: '/Trends/index.html',
                },
                {
                    name: '关于我们',
                    url: '/About/index.html',
                },
                {
                    name: '联系我们',
                    url: '/Contact/index.html',
                },
            ],
        };
    },
};
</script>

<style lang="less">
.m-menus {
    display: flex;
    padding: 22px 0;
    justify-content: space-between;
    .logo {
        height: 36px;
    }
    .menu-icon {
        display: none;
    }
    .navs {
        li {
            display: inline-block;
            margin-left: 50px;
            line-height: 36px;
        }
        a {
            color: #333;
            text-decoration: none;
            &:hover,
            &.active {
                color: #FF290C;
            }
        }
    }
}
</style>



