<template>
    <div class="m-tit">
        <h2>{{tit}}</h2>
        <i class="hr-icon"></i>
    </div>
</template>

<script>
export default {
    props: {
        tit: {
            type: String,
            required: true,
        },
    },
};
</script>

<style lang="less">
.m-tit {
    text-align: center;
    h2 {
        margin-bottom: 8px;
        font-size: 22px;
        font-weight: normal;
        line-height: 1;
    }
    .hr-icon {
        display: inline-block;
        width: 130px;
        height: 10px;
        background: url('../../assets/images/hr-icon.png') no-repeat;
        background-size: 100% 100%;
    }
}
</style>



