<template>
    <div class="m-svideos">
        <c-tit tit="微视频"></c-tit>
        <div class="svideos g-container">
            <ul class="svideos-fix">
                <li class="video" v-for="(video, index) in videos" :key="index">
                    <a :href="video.url" target="_blank">
                        <div class="video-cover">
                            <img :src="video.cover">
                            <div class="video-mask">
                                <i class="e-icon_video"></i>
                            </div>
                        </div>
                        <p class="video-desc">{{video.desc}}</p>
                        <p class="vdieo-count">播放次数 {{video.count}}次</p>
                    </a>
                </li>
            </ul>
            <div class="clearfix"></div>
        </div>
    </div>
</template>

<script>
import CTit from './common/tit';

export default {
    name: 'livestream',
    components: {
        CTit,
    },
    props: {
        videos: {
            type: Array,
            default: () => [],
        },
    },
};
</script>

<style lang="less">
.m-svideos {
    padding-top: 32px;
    background-color: #f6f6f6;
    .svideos {
        margin-top: 30px;
        padding-bottom: 6px;
        a {
            text-decoration: none;
        }
    }
    .svideos-fix {
        margin-left: -8px;
    }
    .video {
        float: left;
        box-sizing: border-box;
        width: 16.66%;
        padding-left: 8px;
        padding-bottom: 22px;
        &:hover {
            .video-mask {
                display: block;
            }
            .video-desc {
                color: #FF3C3D;
            }
        }
    }
    .video-cover {
        position: relative;
        width: 100%;
        height: 99px;
        overflow: hidden;
        img {
            width: 100%;
            min-height: 100%;
            vertical-align: top;
        }
    }
    .video-desc {
        margin-top: 6px;
        font-size: 13px;
        line-height: 1;
        color: #333;
    }
    .vdieo-count {
        margin-top: 6px;
        font-size: 11px;
        line-height: 12px;
        color: #999;
    }
    .video-mask {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, .3);
        // opacity: .3;
        cursor: pointer;
        .e-icon_video {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 34px;
            height: 34px;
            transform: translate(-50%, -50%);
        }
    }
}
</style>



